import React, { useEffect, useState } from "react";

export default function Step4Suggestions({ email, onNext,onBack,brand }) {
    const [suggestions, setSuggestions] = useState(null);
    const [loading, setLoading] = useState(true);
    const colorData = [
  { hex: "#B59B7A", name: "taupe" },
  { hex: "#7C6848", name: "olivebrown" },
  { hex: "#5B4032", name: "deepbrown" }
];
const getBrandLine = (brand) => {
    const trimmed = brand?.trim();
    return trimmed
      ? `Based on ${trimmed} clothing item, here’s what we recommend:`
      : "Based on your clothing item, here’s what we recommend:";
  };
    useEffect(() => {
        // Simulate async fetch delay
        const timeout = setTimeout(() => {
            setSuggestions({
                materials : `Cotton isn’t suitable for swimwear. A recycled nylon-spandex blend (like ECONYL® or REPREVE®) offers:
                       • Eco-friendly sourcing  
                       • Great stretch & recovery  
                       • Vibrant print compatibility  
                       • Quick-dry & durability
                    Prefer softness? We can explore matte, natural-feel swim fabrics.`,
                summary: `• 6 pieces
                 • Dresses category
                 • $100-$250 per piece`,
                fabrics: "Crepe de Chine, Linen blends",
                saleprices : `$100 remains a solid and competitive target based on: 
                           •  Loleia’s pricing ($95–$110 per set) 
                           •  Eco swim fabrics ($8–$15/yd) 
                           •  Simple silhouette but added charm detail 
                          → Final Suggested Sales Price: $100 `,
                cNotes: "Avoid bias cut, Add lining for drape ",
                companionItems:`Build a versatile collection and reduce fabric waste with:
                    • Triangle Halter Top
                    • Cheeky Bottom with Side Rings
                    • Reversible Swim Bandana or Scarf`,
                productionCosts: `$24 estimated production cost per unit. `,
                colors: `Inspired by Loleia’s tropical energy and palm-themed details:

• #49A078 – Jungle Green  
• #FFA66D – Mango Orange  
• #EF5DA8 – Hibiscus Pink  
These colors are vibrant, beach-ready, and print well on swim fabric.`
            });
            setLoading(false);
        }, 1000); // Simulate delay

        return () => clearTimeout(timeout);
    }, []);

    if (loading) {
        return (
             <div className="flex flex-col items-center justify-center min-h-screen text-black/70 font-sans">
        <div className="animate-spin rounded-full h-10 w-10 border-t-2 border-b-2 border-black/70 mb-4"></div>

                In progress...
            </div>
        );
    }

    const colorList = Array.isArray(suggestions.colors)
        ? suggestions.colors
        : suggestions.colors?.split(",") || [];

    const fabricList = Array.isArray(suggestions.fabrics)
        ? suggestions.fabrics
        : suggestions.fabrics?.split(",") || [];

    return (
        <div className="max-w-2xl mx-auto mt-10 p-8 border border-white bg-white/60 backdrop-blur-md rounded-lg shadow-lg font-sans text-white">
             <button
           type = "button"
                    onClick={onBack}
                   className=" pb-1 text-xl font-extrabold text-[#a98a67] ">

               ←
           </button>
              <p className=" ml-5 text-sm text-black mb-2">
                        Step 4 of 4
                    </p>
            <h2 className="text-3xl font-normal mb-1 font-inter text-black text-black">AI-Powered Suggestions</h2>
            <p className="text-sm text-black/70 mb-5 font-[Garamond]">
                 {getBrandLine(brand)}
            </p>
           <div className="mb-6">
                <h3 className="text-base font-semibold mb-2 text-black">Suggested Materials:</h3>
                <p className=" text-[15px] text-left rounded whitespace-pre-line text-black">
                    {suggestions.materials}
                </p>
            </div>
            <div className="mb-6">
                <h3 className="text-base font-semibold mb-2 text-black">Suggested Sales Price:</h3>
                <p className=" text-[15px] text-left rounded whitespace-pre-line text-black">
                    {suggestions.saleprices}
                </p>
            </div>
            <div className="mb-6">
                <h3 className="text-base font-semibold mb-2 text-black">Suggested Cost Production:</h3>
                <p className=" text-[15px] text-left rounded whitespace-pre-line text-black">
                    {suggestions.productionCosts}
                </p>
            </div>
            <div className="mb-6">
            <p className="text-base font-semibold mb-2 text-black">Suggested Color Palette</p>
            <p className=" text-[15px] text-left rounded whitespace-pre-line text-black">Based on your tropical theme and Loleia’s vibrant aesthetic:</p>
            <ul className="list-disc list-inside mt-2 text-black">
                <li><span className="text-[15px] font-semibold">#49A078</span> – Jungle Green</li>
                <li><span className="text-[15px] font-semibold">#FFA66D</span> – Mango Orange</li>
                <li><span className="text-[15px] font-semibold">#EF5DA8</span> – Hibiscus Pink</li>
            </ul>
            <p className="mt-2 text-black">These shades feel fresh, summery, and print beautifully on swim fabrics.</p>
            </div>
            <div className="mb-6">
                <h3 className="text-base font-semibold mb-2 text-black">Suggested Companion Tips:</h3>
                <p className=" text-[15px] text-left rounded whitespace-pre-line text-black">
                    {suggestions.companionItems}
                </p>
            </div>
           {/** 
           <div className="mb-6 ">
  <h3 className="text-base font-semibold mb-2 text-black">Suggested Fabrics:</h3>
  <p className=" text-sm text-left rounded whitespace-pre-line w-full border border-[#b89d7b] bg-transparent px-4 py-2 text-black placeholder-black/50 focus:outline- rounded-md">
                    {suggestions.fabrics}
                </p>
            </div>

            
           
        <div className="mb-6">
  <h3 className="text-base font-semibold mb-2 text-black">Recommended Colors:</h3>

  <div className="flex flex-wrap gap-6">
    {colorData.map((color, index) => (
      <div key={index} className="flex items-center space-x-2">
        <div
          className="w-10 h-10 rounded-full border border-gray-300"
          style={{ backgroundColor: color.hex }}
        ></div>
        
      </div>
    ))}
  </div>
</div>


             <div className="mb-6">
                <h3 className="text-base font-semibold mb-2 text-black">Construction Notes:</h3>
                <p className=" text-sm text-left rounded whitespace-pre-line text-black">
                    {suggestions.cNotes}
                </p>
            </div>
           
           {/*} 
            <div className="mb-6">
                <h3 className="text-base font-semibold mb-2">Recommended Fabrics</h3>
                <ul className="list-disc pl-5 text-sm space-y-1">
                    {fabricList.map((fabric, i) => (
                        <li key={i}>{fabric.trim()}</li>
                    ))}
                </ul>
            </div>

            <div className="mb-10">
                <h3 className="text-base font-semibold mb-2">Construction Tips</h3>
                <p className="text-sm">{suggestions.tips}</p>
            </div>
*/}
           {/** 
            <div className="flex items-center justify-between gap-4">
            <button
                    type = "button"
                    onClick={onNext}
                   className="px-4 py-2 text-l font-bold text-white bg-[#b89d7b] hover:bg-[#a98a67] active:bg-[#8c7152] rounded shadow transition duration-200 rounded-md">
                      Generate Tech Pack
                   </button>
                   <button
                onClick={onNext}
                className="px-4 py-2 text-l font-bold text-white bg-[#b89d7b] hover:bg-[#a98a67] active:bg-[#8c7152] rounded shadow transition duration-200 rounded-md"
            >
                Estimate Cost
            </button>
                   </div>
                   */}
        </div>
    );
}
